<template>
    <ul>
        <h1>공사중</h1>
        <li v-for="exam in exams" v-bind:key="exam.id">
            {{ exam.title }} - {{ exam.start_date }}
        </li>
    </ul>
</template>

<script>
import axios from "axios";
export default {
    data() {
        return {
            exams: []
        }
    },
    created() {
        axios.get("/exams").then((data) => {
            this.exams = data.data;
        }).catch(err => console.error(err));
    }
}
</script>